@import url('https://fonts.googleapis.com/css2?family=Tahoma&display=swap');

body {
    font-family: 'Tahoma', sans-serif;
}

.playground-container {
    background-color: #303030;
    color: #f2f2f2;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.title {
    margin: 0;
    font-size: 2em;
    text-align: center;
    width: 100%;
}

.dropdown-container {
    display: flex;
    align-items: center;
    margin-right: 20px;
    padding: 10px;
}

label {
    margin-right: 10px;
    font-size: 1.2em;
}

select {
    background-color: #f2f2f2;
    color: #303030;
    border: none;
    border-radius: 5px;
    padding: 10px;
    font-size: 1em;
    outline: none;
}

.textbox {
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
    color: #303030;
    border: none;
    outline: none;
    resize: none;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 5px;
}

.textbox:disabled {
    background-color: gray;
}

.textbox-container {
    opacity: 100%;
    padding: 5px;
    font-size: 1em;
    width: 80%;
    height: 70%;
}

.instructions {
    font-size: 1em;
}

button {
    background-color: #f2f2f2;
    color: #303030;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 1em;
    margin-top: 20px;
    cursor: pointer;
    outline: none;
}

.popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #303030;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px gray;
    z-index: 10;
}

.settings-icon {
    font-size: 30px;
    padding: 10px;
}

.Collapsible {
    text-align: center;
}

.Collapsible__contentInner {
    text-align: left;
}

a:any-link {
    color: #fff;
}
